export type Product = {
    id: string;
    name: string;
    price: number;
    categoryId: string;
};

export default function ProductCard({ product }: { product: Product }) {
    return (
        <div className="bg-white p-6 rounded-xl shadow-md border border-gold-main/10 hover:border-gold-main/40 transition-all duration-300 group">
            <div className="flex justify-between items-start mb-4">
                <span className="text-xs font-bold text-brown-light/40 bg-brown-main/5 px-2 py-1 rounded">
                    #{product.id}
                </span>
                <span className="text-xl font-bold text-gold-main">
                    {product.price} ر.س
                </span>
            </div>
            <h3 className="text-xl font-bold text-brown-main group-hover:text-gold-main transition-colors">
                {product.name}
            </h3>
        </div>
    );
}
