"use client";

import { useState } from "react";
import { Category, Product } from "@/lib/data";
import ProductCard from "./ProductCard";

interface MenuDisplayProps {
    categories: Category[];
    products: Product[];
}

export default function MenuDisplay({ categories, products }: MenuDisplayProps) {
    const [selectedId, setSelectedId] = useState("1"); // Default to "الكل" (ID: 1)

    const filteredProducts = selectedId === "1"
        ? products
        : products.filter(p => p.categoryId === selectedId);

    return (
        <div className="max-w-7xl mx-auto px-4 pb-20">
            {/* Categories Filter */}
            <div className="flex flex-wrap justify-center gap-3 mb-12">
                {categories.map((cat) => (
                    <button
                        key={cat.id}
                        onClick={() => setSelectedId(cat.id)}
                        className={`px-6 py-2 rounded-full transition-all duration-300 font-bold ${selectedId === cat.id
                                ? "bg-gold-main text-brown-main shadow-lg"
                                : "bg-brown-main text-gold-main border border-gold-main/30 hover:bg-gold-main/10"
                            }`}
                    >
                        {cat.name}
                    </button>
                ))}
            </div>

            {/* Products Grid */}
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
                {filteredProducts.map((product) => (
                    <ProductCard key={product.id} product={product} />
                ))}
            </div>

            {filteredProducts.length === 0 && (
                <div className="text-center text-brown-light/60 py-20 bg-brown-main/5 rounded-2xl border-2 border-dashed border-gold-main/20">
                    <p className="text-xl italic">لا توجد منتجات في هذا القسم بعد</p>
                </div>
            )}
        </div>
    );
}
